// import logo from './logo.svg';
import './App.css';
import Navbar from './element/Navbar';
import TextForm from './element/TextForm';
// import Alert from './element/Alert';
import { About } from './element/About';
import Homepage from './element/Homepage';
import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route} from "react-router-dom";



function App() {
  const [mode, setMode] = useState('light');
  const toggleMode = () => {
    if (mode === 'light') {
      document.body.style.backgroundColor = '#031c35';
      setMode('dark');
    }
    else {
      setMode('light');
      document.body.style.backgroundColor = 'white';
    }
  }
  return (
    <>
      {/* <Navbar title="TextUtils" about="About TextUtils"/> */}
      <Router>
         <Navbar title="textUtils" mode={mode} toggleMode={toggleMode} />
         {/* <Alert alert="This is your alert" /> */}
        <Routes>
            <Route path="/" element={<Homepage/>}/>
            <Route path="/about" element={<About mode={mode}/>}/>
            <Route path="/textform" element={<TextForm mode={mode} heading="Try TextUtils - Word Counter, Character Counter"/>}/>
        </Routes>
      </Router>
    </>
  );
}

export default App;
