import React, {useState} from 'react'

export default function TextForm(props) {
    const handleUpClick = ()=> {
        // console.log("Clicked");
        let newText = text.toUpperCase();
        setText(newText);
    }
    const handleLoClick = ()=> {
        // console.log("Clicked");
        let newText = text.toLowerCase();
        setText(newText);
    }
    const handleClearClick = ()=> {
        // console.log("Clicked");
        let newText = "";
        setText(newText);
    }
    const handleOnChange = (event)=> {
        // console.log("onChange Clicked" + text);
        setText(event.target.value)
    }
    const [text, setText] = useState('');
    // setText("Deva");
    return (
        <div className="container my-3" style={{color:props.mode==='light'?'#031c35':'white'}}>
            <h2>{props.heading}</h2>
            <div className="mb-3">
                <textarea style={{backgroundColor:props.mode==='light'?'white':'#112339',color:props.mode==='light'?'#031c35':'white'}} className="form-control" id="myBox" value={text} onChange={handleOnChange} rows="8"></textarea>
            </div>
            <button disabled={text.length===0} className="btn btn-primary mx-3 my-2" onClick={handleUpClick}>UPPERCASE</button>
            <button disabled={text.length===0} className="btn btn-primary mx-3 my-2" onClick={handleLoClick}>lowercase</button>
            <button disabled={text.length===0} className="btn btn-primary mx-3 my-2" onClick={handleClearClick}>Clear</button>
            <div className="my-3">
                <h3>Your text summary</h3><hr/>
                <p>{text.split(" ").filter((element)=>{return element.length!==0}).length} words and {text.length} characters</p>
                <p>You will take around {0.008 * text.split(" ").filter((element)=>{return element.length!==0}).length} Minutes to read this text</p>
                <h3>Preview</h3><hr/>
                <p>{text.length>0?text: "Enter your text in textarea to preview it here."}</p>
            </div>
        </div>
    )
}
